<template>
  <div>
    <el-upload
      class="upload-demo"
      ref="upload"
      action="http://localhost:3000/api/upload"
      :file-list="fileList"
      :auto-upload="false"
      :on-success="handleSuccess"
    >
      <el-button 
      slot="trigger"
       size="small"
        type="primary"
        >选取文件</el-button>
      <el-button
        style="margin-left: 10px"
        size="small"
        type="success"
        @click="submitUpload"
        >上传到服务器</el-button
      >
    </el-upload>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fileList: [],
    };
  },
  methods:{
    submitUpload() {
        this.$refs.upload.submit();
      },
      handleSuccess(response){
        console.log(response);
        this.$emit('onUpload',response)
      }
  }
};
</script>

