<template>
  <div class="pag">
    <el-pagination layout="prev, pager, next,jumper" 
    @size-change="handleSize"
    @current-change="handleCurren"
    :total="total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      total:10
    }
  },
  mounted(){
    this.$api.total().then((res)=>{
      // console.log(res.data);
      if(res.data.status === 200){
        this.total = res.data.result[0]['count(*)']
      }
    })
  },
    methods:{
        handleSize(val){
            console.log(val);
        },
        handleCurren(val){
            // console.log(val);
            this.$bus.$emit('page',val);
        }
    }
};
</script>

<style scoped lang="less">
.pag {
  margin-top: 10px;
}
</style>
