<template>
  <div>
    <el-dialog title="编辑产品" :visible.sync="dialogEditVisible" width="60%">
      <el-form
        :model="editorForm"
        ref="editorForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="商品类型:">
          <span class="treeData">{{ treeData.name }}</span>
          <el-button type="text" @click="dialogFormVisible">商品类型</el-button>
          <el-dialog
            title="提示"
            :visible.sync="dialogCategoryVisible"
            width="40%"
            append-to-body
          >
            <ProductTree @onTree="getTreeData" />
            <span slot="footer" class="dialog-footer">
              <el-button type="primary" @click="dialogCategoryVisible = false"
                >确 定</el-button
              >
            </span>
          </el-dialog>
        </el-form-item>
        <el-form-item label="商品名称">
          <el-input v-model="editorForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品卖点">
          <el-input v-model="editorForm.sellPoint" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品价格">
          <el-input v-model="editorForm.price" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品数量">
          <el-input v-model.number="editorForm.num" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="图片上传">
          <img class="upload" :src="upload.url" alt="" />
          <el-button type="text" @click="dialogUpVisibleHandle"
            >上传图片</el-button
          >
          <el-dialog
            title="提示"
            :visible.sync="dialogUpVisible"
            width="40%"
            append-to-body
          >
            <ProductUpload @onUpload="getLoad" />
            <span slot="footer" class="dialog-footer">
              <el-button type="primary" @click="dialogUpVisible = false"
                >确 定</el-button
              >
            </span>
          </el-dialog>
        </el-form-item>
        <el-form-item label="商品描述">
          <ProductWang @onEditor='getEditor' :currenEditor='editor'/>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editorProductHandle"
          >确 定</el-button
        >
        <el-button @click="dialogEditVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import ProductTree from "./ProductTree.vue";
import ProductUpload from "./ProductUpload.vue";
import ProductWang from "./ProductWangEditor.vue";

export default {
  data() {
    return {
      editorForm: {
        name: "",
        num: "",
        sellPoint: "",
        price: "",
      },
      treeData: {
        name:''
      },
      upload: {
        url:''
      },
      editor:{},
      curren:{},
      dialogEditVisible: false,
      dialogCategoryVisible: false,
      dialogUpVisible: false,
    };
  },
  methods: {
    dialogFormVisible() {
      this.dialogCategoryVisible = true;
    },
    dialogUpVisibleHandle() {
      this.dialogUpVisible = true;
    },
    // 读取tree数据
    getTreeData(data) {
      // console.log(data);
      this.treeData = data;
    },
    // 获取上传图片
    getLoad(data) {
      // console.log(data);
      if (data.url) {
        data.url = data.url.replace("upload", "http://localhost:3000");
      }
      this.upload = data;
    },
    // 获取输入文本
    getEditor(val){
        // console.log(val);
        this.editor =val
    },
    // 编辑
    editorProductHandle(){
        this.$api.updateTbItem({
            id:this.curren.id,
            cid:this.treeData.cid,
            title:this.editorForm.name,
            sellPoint:this.editorForm.sellPoint,
            price:this.editorForm.price,
            num:this.editorForm.num,
            desc:this.editor,
            image:this.upload.url

        }).then(res=>{
          if(res.data.status === 200){
            console.log(res.data);
            this.dialogEditVisible = false;
            this.$bus.$emit("refresh",true)
          }
        }).catch(error=>{
          console.log(error);
        })
    }
  },
  components: {
    ProductTree,
    ProductUpload,
    ProductWang,
  },
  mounted() {
    this.$bus.$on("onEditorEvent", row =>{
      this.dialogEditVisible = true;
      this.curren = row;
      this.$api.preUpdateItem({id:row.id}).then(res=>{
        this.treeData.name = res.data.result[0].cid
        this.editorForm.name  = res.data.result[0].title
        this.editorForm.num  = res.data.result[0].num
        this.editorForm.sellPoint  = res.data.result[0].sellPoint
        this.editorForm.price  = res.data.result[0].price
        this.upload.url = res.data.result[0].image
        this.editor = res.data.result[0].descs
        // console.log(res.data);
       
      })
    })
  },
};
</script>

<style scoped>
.location {
  float: left;
}
.tree {
  float: left;
  margin-left: 10px;
}
.upload-img {
  float: left;
  width: 300px;
  margin-left: 10px;
}
.upload {
  float: left;
  width: 300px;
}
.treeData {
  margin-left: 10px;
  float: left;
}
</style>