<template>
  <div class="pro-list">
    <el-table :data="tableData" style="width: 100%">
      <el-table-column show-overflow-tooltip prop="id" label="序号" width="180">
      </el-table-column>
      <el-table-column
        show-overflow-tooltip
        prop="title"
        label="书名"
        width="180"
      >
      </el-table-column>
      <el-table-column
        show-overflow-tooltip
        prop="sellPoint"
        label="介绍"
        width="450"
      >
      </el-table-column>
      <el-table-column
        show-overflow-tooltip
        prop="price"
        label="价格"
        width="180"
      >
      </el-table-column>
      <el-table-column
        show-overflow-tooltip
        prop="num"
        label="商品数量"
        width="180"
      >
      </el-table-column>
      <el-table-column
        show-overflow-tooltip
        prop="descs"
        label="商品描述"
        width="180"
      >
      <template slot-scope="scope">
        <div v-html="scope.row.descs"></div>
      </template>
      </el-table-column>
      <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
    };
  },

  mounted() {
    this.Pages(1);
    this.$bus.$on("page", (val) => {
      // console.log(val);
      this.Pages(val);
    });
    this.$bus.$on('searchData',data =>{
      this.tableData = data
    });
    this.$bus.$on('refresh',()=>{
      window.location.reload()
    })
  },
  methods: {
    
    Pages(page) {
      this.$api.selectTbItemAllByPage({ page }).then((res) => {
        // console.log({ page });
        if (res.data.status === 200) {
          this.tableData = res.data.result;
        }
      });
    },
    handleEdit(index, row) {
        // console.log(index, row);
        this.$bus.$emit('onEditorEvent',row)
      },
      // 删除
      handleDelete(index, row) {
        console.log(index, row);
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        this.$api.deleteItemById({id:row.id}).then(res=>{
          if(res.data.status === 200){
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          window.location.reload();
        }else{
          this.$message({
            type: ' error	',
            message: '删除失败!'
          });
        }
        })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      }
  },
};
</script>

